import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import CourseLayout from "../components/courseLayout"

const CourseMaterial = ({ pageContext: { course } }) => (

  <Layout>
  	<CourseLayout>
    	<h2>Materials</h2>
    	<p className="pHomework">Course materials</p>

    	<div>
    		{course.materials.map(material => 
    			<div>
    				<h6>{material.title}</h6>
    				<p>{material.date}</p>
    				<p>{material.description}  <a href={material.url} target="blank">[Text/Slides]</a>
    				</p>
    				<hr />
    			</div>
    			)
    		}
    	</div>


    </CourseLayout>
  </Layout>

)

export default CourseMaterial

